  display: block; }

main {
  width: 66.66667%;
  display: block;
  float: left;
  box-sizing: border-box;
  padding: 0 20px; }
  main:first-of-type {
    padding-left: 0; }
  main:last-of-type {
    padding-right: 0; }

.inner {
  max-width: 1450px;
  margin: 0 auto; }

.cf::after {
  content: '';
  clear: both;
  display: block; }

.table .row::after {
  content: '';
  clear: both;
