/**
 * Responsive
 **/
@media all and (max-width: 800px) {
  /* navigation */
  .hamburglar {
    display: block; }
  nav.mainnav {
    display: none; }
    nav.mainnav li {
      display: block;
      margin: 0; }
      nav.mainnav li a {
        background: #EEEEEE;
        color: #000000;
        border-bottom: 1px solid #BBBBBB; }
        nav.mainnav li a:hover, nav.mainnav li a.current, nav.mainnav li a.section {
          background: #BBBBBB; }
      nav.mainnav li .menu.level-2 {
        position: relative; }
  section .row .col {
    width: 33.33333%;
    display: block;
    float: left;
    box-sizing: border-box;
    padding: 0 1rem; }
    section .row .col:first-of-type {
      padding-left: 0; }
    section .row .col:last-of-type {
      padding-right: 0; }
  section.grid .item {
    width: 48%;
    box-sizing: border-box;
    margin: 0 2%; }
    section.grid .item:nth-of-type(n), section.grid .item:nth-of-type(1n+1) {
      margin: 4% 2% 0;
      clear: none; }
    section.grid .item:nth-of-type(2n+1) {
      margin-left: 0;
      clear: both; }
    section.grid .item:first-of-type {
      margin-top: 0;
      margin-left: 0; }
    section.grid .item:nth-of-type(1) {
      margin-top: 0; }
    section.grid .item:nth-of-type(2) {
      margin-top: 0; }
    section.grid .item:nth-of-type(2n) {
      margin-right: 0;
      position: relative; }
      section.grid .item:nth-of-type(2n):after {
        clear: both; }
    section.grid .item:last-of-type {
      margin-bottom: 0; } }

@media all and (max-width: 600px) {
  section .row .col {
    width: 100%;
    display: block;
    float: left;
    box-sizing: border-box;
    padding: 0 1rem; }
    section .row .col:first-of-type {
      padding-left: 0; }
    section .row .col:last-of-type {
      padding-right: 0; }
  section.grid .item {
    width: 100%;
    box-sizing: border-box;
    margin: 0 2%; }
    section.grid .item:nth-of-type(n), section.grid .item:nth-of-type(1n+1) {
      margin: 4% 2% 0;
      clear: none; }
    section.grid .item:nth-of-type(1n+1) {
      margin-left: 0;
      clear: both; }
    section.grid .item:first-of-type {
      margin-top: 0;
      margin-left: 0; }
    section.grid .item:nth-of-type(1) {
      margin-top: 0; }
    section.grid .item:nth-of-type(1n) {
      margin-right: 0;
      position: relative; }
      section.grid .item:nth-of-type(1n):after {
        clear: both; }
    section.grid .item:last-of-type {
      margin-bottom: 0; } }

/**
 * Global typographic styles
 *
 * All typography formatting and general font settings
 * belong in this file. For unique blocks and widgets, etc
 * you should put those in their own component file
 **/
body,
html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px; }

h1 {
  font-size: 3rem;
  padding: 0.5rem 0;
  font-weight: 600; }

h2 {
  font-size: 2.2rem;
  padding: 0.5rem 0;
  font-weight: 600; }

h3 {
  font-size: 1.8rem;
  padding: 0.5rem 0;
  font-weight: 400; }

h4 {
  font-size: 1.4rem;
  padding: 0.5rem 0;
  font-weight: 400; }

h5 {
  font-size: 1rem;
  padding: 0.5rem 0;
  font-weight: 600; }

p {
  padding: 0.4rem 0; }

ul,
ol {
  padding-left: 24px; }
  ul li,
  ol li {
    padding: 0.2em 0; }

a {
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out; }

input,
select,
textarea,
option,
button {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1rem;
  padding: 10px 16px;
  box-sizing: border-box;
  border: 1px solid #BBBBBB;
  width: 100%; }
  input:focus, input:active,
  select:focus,
  select:active,
  textarea:focus,
  textarea:active,
  option:focus,
  option:active,
  button:focus,
  button:active {
    border-color: #06b8e4; }

.field {
  padding-bottom: 15px; }
  .field label {
    display: block;
    padding-bottom: 4px; }

button,
.button,
input[type="submit"] {
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  background: #06b8e4;
  color: #FFFFFF;
  cursor: pointer;
  text-decoration: none;
  font-size: 1rem;
  font-weight: 700;
  border: 0;
  width: auto;
  padding: 10px 30px; }
  button:hover,
  .button:hover,
  input[type="submit"]:hover {
    background: #0590b2; }

::-webkit-input-placeholder {
  color: rgba(0, 0, 0, 0.25); }

:-moz-placeholder {
  color: rgba(0, 0, 0, 0.25); }

::-moz-placeholder {
  color: rgba(0, 0, 0, 0.25); }

:-ms-input-placeholder {
  color: rgba(0, 0, 0, 0.25); }

table {
  margin: 10px 0;
  width: 100%;
  border-spacing: 0;
  border-collapse: collapse; }
  table tr {
    border-bottom: 1px solid #BBBBBB; }
    table tr th,
    table tr td {
      text-align: left;
      padding: 4px 8px;
      vertical-align: top; }
      table tr th:not(:first-child),
      table tr td:not(:first-child) {
        border-left: 1px solid #BBBBBB; }
    table tr th {
      font-weight: 600;
      background: #EEEEEE;
      vertical-align: bottom; }

